





























































import { Component, Vue, Prop } from 'vue-property-decorator';

@Component({})

export default class Champion extends Vue {
  @Prop({ type: Array, default: [] }) public readonly championData!: any[];
  @Prop({ type: Number, default: 0 }) public readonly rank!: number; // default value `0` -> first rank
  private count?: any = 0;
  private isLoading: boolean = false;
  private themeColor: any = {
    'classic-red': '#e63333',
    'classic-blue': '#11548f',
    'noble-black-gold': '#865920',
    'simple-platinum': '#4f4f4f',
  };

  get getThemeColor() {
    return this.$store.state.themeColor;
  }

  private onRefresh() {
    setTimeout(() => {
      this.isLoading = false;
      this.count++;
    }, 1000);
  }

  private goPage(name: string) {
    this.$router.push({name: name});
  }
  private calculLenght(item: any,index: any){
      var prevouisItem=0
      var widthchampion=0
      var borderwidth=''
      var i =1
      var leftSide=''
      var borderHeight=30;
      while(prevouisItem==0){
          if(this.championData[index-i].nums!=''){
              prevouisItem=this.championData[index-i].nums
          }
          else{
             borderHeight=borderHeight+30 
          }
          i++
      }
      if(item.nums>prevouisItem){
          widthchampion=(30*(item.nums-prevouisItem-1))+20
          borderwidth='border-width: '+borderHeight+'px 0px 0px '+widthchampion+'px;'
          leftSide='left: auto;right: 13px; border-color: transparent '+ this.themeColor[this.getThemeColor] +' transparent;top: -'+(borderHeight - 10)+'px;'
   
    
      }
      else if(item.nums==prevouisItem){
          borderwidth='border-left: 2px solid '+ this.themeColor[this.getThemeColor] +';top:-'+(borderHeight - 10)+'px;left: 10px;height: '+(borderHeight - 30)+'px;'
         
          }
        else{
          widthchampion=(30*(prevouisItem-item.nums-1))+20
            borderwidth='border-width: 1px 1px '+borderHeight+'px '+widthchampion+'px;top:-'+(borderHeight - 10)+'px;'
      }
      return borderwidth+leftSide
  }
  private calculLenghtI(item: any,index: any){
       var prevouisItem=0
      var widthchampion=0
      var borderwidth=''
      var i =1
      var borderHeight=30;
      while(prevouisItem==0){
          if(this.championData[index-i].nums!=''){
              prevouisItem=this.championData[index-i].nums
          }
          else{
             borderHeight=borderHeight+30 
          }
          i++
      }
      if(item.nums>prevouisItem){
          widthchampion=(30*(item.nums-prevouisItem-1))+20
          borderwidth='right: 0px;top: -'+(borderHeight - 1)+'px;left: auto;border-color: transparent #fff;border-width: '+borderHeight+'px 1px 1px '+widthchampion+'px;'
   
    
      }
      else if(item.nums==prevouisItem){
          borderwidth='display:none'
         
          }
      else{
          widthchampion=(30*(prevouisItem-item.nums-1))+20
          borderwidth='border-width: 1px 1px '+borderHeight+'px '+widthchampion+'px;'
      }
      return borderwidth
  }
}
